@import '../lib/colors.scss';

.header{
    padding: 25px 50px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #efefff;
    .logo{
        margin-right: 55px;
    }
    &__menu{
        display: flex;
        gap: 15px;
        align-items: center;
        span,
        a{
            padding: 10px 15px;
            font-size: 16px;
            font-family: 'semibold';
            display: inline-block;
            line-height: 24px;
            color: #333;
            cursor: pointer;
            &:hover{
                color: $purple;
            }
            &:active{
                padding-bottom: 8px;
                border-bottom: 2px solid $purple;
            }
        }
    }
    &__dropdown{
        display: none;
    }
    .btn{
        margin-left: auto;
    }
}